import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material';
import { DialogOrderDetailsComponent } from './dialog-orders/dialog-order-details.component';
import { OrderService } from '../services/order.service';
import { UserService } from '../services/user.service';
@Component({
    selector: 'app-orders',
    templateUrl: './orders.component.html',
    styleUrls: ['./orders.component.scss']
})
export class OrdersComponent implements OnInit {
    currentUser;
    orders = [];
    constructor(
        public dialog: MatDialog,
        private orderService: OrderService,
        private userService: UserService,
    ) {
        this.loadProfile();
    }
    ngOnInit(): void {
        this.getListOrder();
    }
    loadProfile() {
        this.currentUser = this.userService.getCurrentUser();
    }
    getListOrder() {
        this.orderService.getUserOrder(this.currentUser.id_user).subscribe(data => {
            if (data) {
                this.orders = data;
                console.log(this.orders);
            }
        });
    }

    showDetails(order: any): void {
        const dialogRef = this.dialog.open(DialogOrderDetailsComponent, {
            data: order,
            maxHeight: '98vh',
            panelClass: 'dialog-order'

        });
        dialogRef.afterClosed().subscribe(() => {
        });
    }
}
