import { Component, OnInit } from '@angular/core';
import { ProductService } from '../../services/product.service';
import { UserService } from '../../services/user.service';
import { ShoppingCartComponent } from '../shopping-cart/shopping-cart.component';
import { ToastrService } from '../../../../node_modules/ngx-toastr';
import { DirectionService } from '../../services/direction.service';
import { MatDialog } from '../../../../node_modules/@angular/material';

@Component({
  selector: 'app-shopping-bounes',
  templateUrl: './shopping-bounes.component.html',
  styleUrls: ['./shopping-bounes.component.scss'],
})
export class ShoppingBounesComponent implements OnInit {
  currentUser;
  listItem: Array<any> = [];
  listMenu = [
    { name: 'Beef Brahman', id: '1', route: '/app/shopping/beef/1' },
    { name: 'Beef Red Sindhi', id: '2', route: '/app/shopping/beef/2' },
    { name: 'Beef USA', id: '3', route: '/app/shopping/beef/3' },
    { name: 'Beef Kobe', id: '4', route: '/app/shopping/beef/4' },
    { name: 'Milk', id: 'milk', route: '/app/shopping/milk' },
    { name: 'Bounes', id: 'bounes', route: '/app/shopping/bounes' },
  ];
  constructor(
    private productService: ProductService,
    private userService: UserService,
    private toastr: ToastrService,
    private directionService: DirectionService,
    private dialog: MatDialog,
  ) {
    this.loadProfile();
  }

  ngOnInit(): void {
    this.getListItem();
  }
  loadProfile() {
    this.currentUser = this.userService.getCurrentUser();
  }
  getListItem() {
    this.productService.getAllBeefByPathBySpecies(this.currentUser.area, '3', 'any', 'any').subscribe(res => {
      if (res.message === false) {
        return;
      }
      res.map(item => {
        item.type = 3;
        this.listItem.push(item);
      });
      console.log(this.listItem);
    });
  }
  addToCart(item) {
    this.saveItem(item, res => {
      if (res === true) {
        this.dialog.open(ShoppingCartComponent, {
          width: 'auto',
          height: 'auto',
          data: JSON.parse(localStorage.getItem('orderProduct'))
        });
      }
    });
  }
  saveItem(item, callback) {
    const orderProduct: any[] = JSON.parse(localStorage.getItem('orderProduct'));
    const index = orderProduct.findIndex(o => o.id_bounes === item.id_bounes);
    if (index > -1) {
      this.toastr.error('Thông báo!', 'Sản phẩm đã có trong giỏ hàng', {
        progressBar: true,
        positionClass: 'toast-bottom-right',
        timeOut: 2000
      });
      callback(false);
    } else {
      orderProduct.push(item);
      localStorage.setItem('orderProduct', JSON.stringify(orderProduct));
      this.directionService.sendData(JSON.parse(localStorage.getItem('orderProduct')));
      callback(true);
    }
  }

}
