import { ErrorStateMatcher } from '@angular/material/core';
import { FormControl, FormGroupDirective, NgForm, Validators, FormGroup } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';
import { MouseEvent } from '@agm/core';
import { CustomerService } from '../../services/customer.service';
import { LoadingService } from '../../services/loading.service';
import { AuthService, GoogleLoginProvider } from '../../../../node_modules/angularx-social-login';
export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}
@Component({
  selector: 'app-registration',
  templateUrl: './registration.component.html',
  styleUrls: ['./registration.component.scss']
})
export class RegistrationComponent implements OnInit {

  hide = true;
  matcher = new MyErrorStateMatcher();
  form: FormGroup;
  ngOnInit(): void {
    this.form = new FormGroup({
      email: new FormControl('', [Validators.required, Validators.email]),
      password: new FormControl('', [Validators.required]),
      name: new FormControl('', [Validators.required]),
      birthday: new FormControl(''),
      gender: new FormControl('1', [Validators.required]),
      area: new FormControl('HCM', [Validators.required]),
      language: new FormControl('vi', [Validators.required]),
      avatar: new FormControl('http://i1150.photobucket.com/albums/o614/manhtung159/th_none-user_zpsz4mxlhzn.jpg'),
      address: new FormControl('', [Validators.required]),
      phone: new FormControl('', [Validators.required]),
    });
  }

  constructor(
    private customerService: CustomerService,
    private toastr: ToastrService,
    public loadingService: LoadingService,
    private authService: AuthService,
    private router: Router
  ) {

  }
  onSubmit() {
    this.loadingService.start();
    console.log(this.form);
    setTimeout(() => {
      if (this.form.valid) {
        this.customerService.checkSignup({ email: this.form.value.email }).subscribe(result => {
          if (result.message === true) {
            this.toastr.error('Please try other email!', 'Email existed', {
              progressBar: true,
              positionClass: 'toast-bottom-right',
              timeOut: 3000
            });
          } else if (result.message === false) {
            this.customerService.customerRegister(this.form.value).subscribe(data => {
              if (data.message === 'Error') {
                this.toastr.error(data.message, 'Something went wrong please try again!', {
                  progressBar: true,
                  positionClass: 'toast-bottom-right',
                  timeOut: 2000
                });
              } else {
                this.toastr.success('', data.message, {
                  progressBar: true,
                  positionClass: 'toast-bottom-right',
                  timeOut: 3000
                });
              }
              this.loadingService.stop();
            });
          }
        });

      } else {
        this.loadingService.stop();
      }
    }, 1000);


  }

  signInWithGoogle(): void {
    this.loadingService.start();
    setTimeout(() => {
      this.authService.authState.subscribe((user) => {
        if (user) {
          console.log(user);
          this.customerService.customerLoginWithGoogle(user).subscribe(result => {
            if (result.message === true) {
              localStorage.setItem('CurrentUser', JSON.stringify(result.data[0]));
              this.loadingService.stop();
              this.router.navigate(['/app']);
            } else {
              this.loadingService.stop();
              this.toastr.error('Login False!', 'Tài khoản đăng nhập không chính xác...', {
                progressBar: true,
                positionClass: 'toast-bottom-right',
                timeOut: 2000
              });
            }
          });
        } else {
          this.authService.signIn(GoogleLoginProvider.PROVIDER_ID);
        }
      });
    }, 500);
  }

}
