import { Component, OnInit, AfterViewInit } from '@angular/core';
import { UserService } from '../services/user.service';
import { FormGroup, FormControl, Validators } from '../../../node_modules/@angular/forms';
import { CustomerService } from '../services/customer.service';
import { ToastrService } from '../../../node_modules/ngx-toastr';
import { TranslateService } from '../../../node_modules/@ngx-translate/core';

@Component({
    selector: 'app-profiles',
    templateUrl: './profiles.component.html',
    styleUrls: ['./profiles.component.scss']
})
export class ProfilesComponent implements OnInit, AfterViewInit {

    currentUser;
    form: FormGroup;
    listUserAddress: Array<any> = [];
    constructor(
        private userService: UserService,
        private customerService: CustomerService,
        private translateService: TranslateService,
        private toastr: ToastrService,
    ) {
        this.loadProfile();
    }
    ngOnInit(): void {
        this.initForm();
        this.getListUserAddress();
    }
    ngAfterViewInit(): void {
        setTimeout(() => {
            this.setValue();
        }, 100);
    }
    getListUserAddress() {
        this.customerService.getListDeliveryAddress(this.currentUser.id_user).subscribe(data => {
            if (data) {
                this.listUserAddress = data;
            }
        });
    }
    initForm() {
        this.form = new FormGroup({
            password: new FormControl('', [Validators.required]),
            name: new FormControl('', [Validators.required]),
            birthday: new FormControl(''),
            gender: new FormControl('', [Validators.required]),
            area: new FormControl('', [Validators.required]),
            language: new FormControl('', [Validators.required]),
            main_address: new FormControl('', [Validators.required]),
        });
    }
    setValue() {
        this.form.controls.name.setValue(this.currentUser.name);
        this.form.controls.password.setValue(this.currentUser.password);
        this.form.controls.birthday.setValue(this.currentUser.birthday);
        this.form.controls.gender.setValue(this.currentUser.gender);
        this.form.controls.area.setValue(this.currentUser.area);
        this.form.controls.language.setValue(this.currentUser.language);
        this.form.controls.main_address.setValue(this.currentUser.main_address);
    }
    loadProfile() {
        this.currentUser = this.userService.getCurrentUser();
        this.translateService.setDefaultLang(this.currentUser.language);
    }

    onSubmit() {
        console.log(this.form);
        if (this.form.valid) {
            this.customerService.updateProfile(this.currentUser.id_user, this.form.value).subscribe(data => {
                if (data.error === true) {
                    this.toastr.error(data.message, 'Something went wrong please try again!', {
                        progressBar: true,
                        positionClass: 'toast-bottom-right',
                        timeOut: 2000
                    });
                } else {
                    // tslint:disable-next-line:max-line-length
                    this.customerService.customerLogin({ email: this.currentUser.email, password: this.form.value.password }).subscribe(result => {
                        if (result.message === true) {
                            this.toastr.success('', 'Cập nhật thông tin thành công!', {
                                progressBar: true,
                                positionClass: 'toast-bottom-right',
                                timeOut: 3000
                            });
                            localStorage.setItem('CurrentUser', JSON.stringify(result.data[0]));
                            this.loadProfile();
                            this.initForm();
                            setTimeout(() => {
                                this.setValue();
                            }, 100);
                        }
                    });
                }
            });

        }
    }
}
