import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { MatSort, MatHorizontalStepper, MatButton } from '../../../../node_modules/@angular/material';
import { DirectionService } from '../../services/direction.service';
import { Router } from '../../../../node_modules/@angular/router';
import { FormBuilder, FormGroup } from '../../../../node_modules/@angular/forms';
import { TranslateService } from '../../../../node_modules/@ngx-translate/core';
import { UserService } from '../../services/user.service';
import { CustomerService } from '../../services/customer.service';
import { ToastrService } from '../../../../node_modules/ngx-toastr';
import { LoadingService } from '../../services/loading.service';
import { OrderService } from '../../services/order.service';

@Component({
  selector: 'app-checkout',
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.scss']
})
export class CheckoutComponent implements OnInit, AfterViewInit {
  totalprice = 0;
  totalpoint = 0;
  listUserAddress: Array<any> = [];
  currentUser;
  pathName = [
    '',
    'Chuck',
    'Ribs',
    'Loin',
    'Round',
    'Brisket',
    'Plate',
    'Flank',
  ];
  weightName = [
    '',
    '250',
    '500',
    '1000',
  ];
  data: any;
  bounes = [];
  Order = {
    id_user: '',
    receiver: '',
    address: '',
    phone: '',
    total_price: 0,
    type_buy: '1',
    type_pay: '0',
    details: []
  };
  error = false;
  @ViewChild('stepper') stepper: MatHorizontalStepper;
  constructor(
    private directionService: DirectionService,
    private translateService: TranslateService,
    private userService: UserService,
    private customerService: CustomerService,
    public loadingService: LoadingService,
    private orderService: OrderService,
    private toastr: ToastrService,
    private router: Router) {
    this.loadProfile();
    this.data = JSON.parse(localStorage.getItem('orderProduct'));
    if (this.data.length < 1) {
      this.router.navigate(['/app']);
    } else {
      console.log(this.data);
      this.Order.details = this.data;
    }
  }

  ngOnInit() {
    this.getListUserAddress();
  }
  ngAfterViewInit(): void {
    setTimeout(() => {
      this.computeTotalPrice();
    }, 888);
  }
  loadProfile() {
    this.currentUser = this.userService.getCurrentUser();
    console.log(this.currentUser);
  }
  getListUserAddress() {
    this.customerService.getListDeliveryAddress(this.currentUser.id_user).subscribe(data => {
      if (data) {
        this.listUserAddress = data;
        this.initForm(this.currentUser.main_address);
      }
    });
  }
  initForm(id_address) {
    const index = this.listUserAddress.map(m => m.id_address).indexOf(id_address);
    this.Order.id_user = this.currentUser.id_user;
    this.Order.receiver = this.listUserAddress[index].tagName;
    this.Order.address = this.listUserAddress[index].address;
    this.Order.phone = this.listUserAddress[index].phone;
  }
  selectAddress(event) {
    console.log(event);
    this.initForm(event.value);
  }
  computeTotalPrice() {
    this.totalprice = 0;
    this.bounes = [];
    this.data.forEach(element => {
      if (element.type < 3) {
        this.totalprice += element.priceItemBuy;
      } else {
        this.bounes.push(element);
      }
    });
    this.Order.total_price = this.totalprice;
    this.Order.details = this.data;
    this.computeBounesPoint();
  }
  computeBounesPoint() {
    this.totalpoint = 0;
    this.bounes.forEach(element => {
      this.totalpoint += element.point;
    });
  }
  delete(item) {
    if (item.type < 3) {
      const index = this.data.map(m => m.bCode).indexOf(item.bCode);
      console.log(index);
      this.data.splice(index, 1);
      if (this.data.length <= 0) {
        this.router.navigate(['/app']);
      }
      localStorage.setItem('orderProduct', JSON.stringify(this.data));
      this.directionService.sendData(this.data);
      this.computeTotalPrice();
    } else {
      const index = this.data.map(m => m.id_bounes).indexOf(item.id_bounes);
      console.log(index);
      this.data.splice(index, 1);
      if (this.data.length <= 0) {
        this.router.navigate(['/app']);
      }
      localStorage.setItem('orderProduct', JSON.stringify(this.data));
      this.directionService.sendData(this.data);
      this.computeTotalPrice();
    }
  }
  filterProduct(data): Array<any> {
    return data.filter(m => m.type !== 3);
  }
  computeTotal(item): number {
    item.priceItemBuy = (item.buy * item.price) - (((item.buy * item.price) * item.saleoff) / 100);
    return item.priceItemBuy;
  }
  changeStep(event) {
    console.log(event);
  }
  purchase(btn: MatButton) {
    btn.disabled = true;
    this.error = false;
    if (localStorage.getItem('orderProduct')) {
      const finalOrder = JSON.parse(JSON.stringify(this.Order));
      if (finalOrder.type_pay === '0') {
        finalOrder.total_price += 30000;
      }
      this.checkBounes(data => {
        if (data === true) {
          this.loadingService.start();
          document.getElementById('divFull').style.pointerEvents = 'none';
          document.getElementById('divFull').style.opacity = '.8';
          console.log(finalOrder);
          this.orderService.postUserOrder(finalOrder).subscribe(result => {
            if (result.message === true) {
              this.loadingService.stop();
              document.getElementById('divFull').style.pointerEvents = 'unset';
              document.getElementById('divFull').style.opacity = '1';

              this.data = [];
              this.Order = {
                id_user: '',
                receiver: '',
                address: '',
                phone: '',
                total_price: 0,
                type_buy: '1',
                type_pay: '0',
                details: []
              };
              localStorage.setItem('orderProduct', JSON.stringify(this.data));
              this.directionService.sendData(this.data);
              this.computeTotalPrice();
              this.getNewUserPoint();
              this.toastr.success('Nhân viên Agrilego sẽ gọi cho bạn sau vài phút, cảm ơn!', 'Đặt hàng thành công!', {
                progressBar: true,
                positionClass: 'toast-bottom-right',
                timeOut: 4000
              });
              setTimeout(() => {
                this.router.navigateByUrl('/app/orders');
              }, 3000);
            }
          });
        } else {
          btn.disabled = false;
          this.toastr.error('', 'Không thể thực hiện!!', {
            progressBar: true,
            positionClass: 'toast-bottom-right',
            timeOut: 3000
          });
        }
      });
    }

  }

  checkBounes(callback) {
    let checkBounes = 0;
    if (this.bounes.length > 0) {
      this.bounes.forEach((element, index) => {
        checkBounes += element.point;
        if (checkBounes > this.currentUser.point) {
          this.toastr.error('Điểm hiện tại của bạn: ' + this.currentUser.point, 'Điểm đổi thưởng của bạn không đủ để thực hiện!!', {
            progressBar: true,
            positionClass: 'toast-bottom-right',
            timeOut: 3000
          });
          this.error = true;
          callback(false);
        }
        if (index === (this.bounes.length - 1) && this.error === false) {
          callback(true);
        }
      });
    } else {
      callback(true);
    }

  }

  getNewUserPoint() {
    this.customerService.customerLogin({ email: this.currentUser.email, password: this.currentUser.password }).subscribe(result => {
      if (result.message === true) {
        localStorage.setItem('CurrentUser', JSON.stringify(result.data[0]));
        this.loadProfile();
      }
    });
  }
}
