
import { map } from 'rxjs/operators';
import { Injectable } from '@angular/core';
// tslint:disable-next-line:import-blacklist
import { Observable, BehaviorSubject } from 'rxjs';


import { URLSearchParams } from '@angular/http';
import { ApiService } from './api.service';
import { QueryBcodeList } from '../models/query.model';

@Injectable()
export class ProductService {
    constructor(
        private apiService: ApiService
    ) { }
    // chưa làm
    // query(queryList: QueryBcodeList): Observable<any> {
    //     const params: URLSearchParams = new URLSearchParams();
    //     Object.keys(queryList.filters)
    //         .forEach((key) => {
    //             params.set(key, queryList.filters[key]);
    //         });
    //     return this.apiService.getQuery('/bcode', params).pipe(map(data => data));
    // }
    get(id: string): Observable<any> {
        return this.apiService.get('/bcode/' + id).pipe(
            map(result => result));
    }
    post(id: string, canbuy: string): Observable<any> {
        return this.apiService.post('/bcode/' + id + '/' + canbuy).pipe(
            map(result => result));
    }
    getAllBeefByArea(area: string, type: string): Observable<any> {
        return this.apiService.get('/bcode/' + area + '/' + type).pipe(
            map(result => result));
    }
    getAllBeefByAreaByPath(idArea: string, type: string, type_beef: string): Observable<any> {
        return this.apiService.get('/bcode/' + idArea + '/' + type + '/' + type_beef).pipe(
            map(result => result));
    }
    getAllBeefByApoint(idArea: string, type: string, idApoint: string): Observable<any> {
        return this.apiService.get('/bcode/' + idArea + '/' + type + '/0/' + idApoint).pipe(
            map(result => result));
    }
    getAllBeefByPathBySpecies(area: string, type: string, type_path: string, species: string): Observable<any> {
        return this.apiService.get('/bcode/' + area + '/' + type + '/' + type_path + '/' + species).pipe(
            map(result => result));
    }
    getBcodeSaleOff(data: any): Observable<any> {
        return this.apiService.post('/bcode/saleoff', data).pipe(
            map(result => result));
    }
    search(data: any): Observable<any> {
        return this.apiService.post('/bcode/search', data).pipe(
            map(result => result));
    }
}
