import { Component, OnInit } from '@angular/core';
import { ToastrService } from '../../../../node_modules/ngx-toastr';
import { LoadingService } from '../../services/loading.service';
import { FormGroup, FormControl, FormGroupDirective, NgForm, Validators } from '../../../../node_modules/@angular/forms';
import { ErrorStateMatcher } from '../../../../node_modules/@angular/material';
import { CustomerService } from '../../services/customer.service';
@Component({
  selector: 'app-resetpassword',
  templateUrl: './resetPassword.component.html',
  styleUrls: ['./resetPassword.component.scss']
})
export class ResetPasswordComponent implements OnInit {

  form: FormGroup;
  matcher = new MyErrorStateMatcher();
  constructor(
    private toastr: ToastrService,
    public loadingService: LoadingService,
    private customerService: CustomerService,
  ) {
  }
  ngOnInit(): void {
    this.form = new FormGroup({
      email: new FormControl('', [Validators.required, Validators.email]),
    });
  }
  onSubmit() {
    this.loadingService.start();
    console.log(this.form);
    setTimeout(() => {
      if (this.form.valid) {
        this.customerService.reqResetPassword({ email: this.form.value.email }).subscribe(result => {
          if (result.message === false) {
            this.toastr.error('Send False!', 'Email này chưa được đăng ký...', {
              progressBar: true,
              positionClass: 'toast-bottom-right',
              timeOut: 2000
            });
          } else {
            this.toastr.success('Please check your email', result.message, {
              progressBar: true,
              positionClass: 'toast-bottom-right',
              timeOut: 2000
            });
          }
          this.loadingService.stop();
        });
      } else {
        this.loadingService.stop();
      }
    }, 1000);

  }
}
export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}
