import { Component, OnInit, OnDestroy } from '@angular/core';
import { ProductService } from '../../services/product.service';
import { UserService } from '../../services/user.service';
import { NgxCarousel } from '../../../../node_modules/ngx-carousel';
import { ToastrService } from '../../../../node_modules/ngx-toastr';
import { DirectionService } from '../../services/direction.service';
import { MatDialog } from '../../../../node_modules/@angular/material';
import { ShoppingCartComponent } from '../shopping-cart/shopping-cart.component';

@Component({
  selector: 'app-shopping-milk',
  templateUrl: './shopping-milk.component.html',
  styleUrls: ['./shopping-milk.component.scss'],
})
export class ShoppingMilkComponent implements OnInit, OnDestroy {
  currentUser;
  listItem: Array<any> = [];
  listMenu = [
    { name: 'Beef Brahman', id: '1', route: '/app/shopping/beef/1' },
    { name: 'Beef Red Sindhi', id: '2', route: '/app/shopping/beef/2' },
    { name: 'Beef USA', id: '3', route: '/app/shopping/beef/3' },
    { name: 'Beef Kobe', id: '4', route: '/app/shopping/beef/4' },
    { name: 'Milk', id: 'milk', route: '/app/shopping/milk' },
    { name: 'Bounes', id: '', route: '/app/shopping/bounes' },
  ];
  imgMilk = [
    '',
    'http://i1150.photobucket.com/albums/o614/manhtung159/Alego/milk-bottle_zpswwdft8yl.png',
    'http://i1150.photobucket.com/albums/o614/manhtung159/Alego/tab-milk_zpszcg0q0b4.png',
    'http://i1150.photobucket.com/albums/o614/manhtung159/Alego/milk_zpslz1bqaa8.png'
  ];
  weightName = [
    '',
    '250',
    '500',
    '1000',
  ];
  imgags = [
    'http://i1150.photobucket.com/albums/o614/manhtung159/Alego/natures-banner_zpsuklq2ieq.jpg',
    'http://i1150.photobucket.com/albums/o614/manhtung159/Alego/WEB_Banner_Milk_zpspsxcchxg.png',
    'http://i1150.photobucket.com/albums/o614/manhtung159/Alego/banner_zpsf0gre8tm.jpg',
    'http://i1150.photobucket.com/albums/o614/manhtung159/Alego/banner2_zpst6dqnuts.jpg',
  ];

  public carouselBannerItems: Array<any> = [];
  public carouselBanner: NgxCarousel;
  id: any;
  constructor(
    private productService: ProductService,
    private userService: UserService,
    private toastr: ToastrService,
    private directionService: DirectionService,
    private dialog: MatDialog,
  ) {
    this.loadProfile();
  }

  ngOnInit(): void {
    this.getListItem();
    this.initCarousel();
    this.id = setInterval(() => {
      this.getListItem();
    }, 10000);
  }
  ngOnDestroy(): void {
    if (this.id) {
      clearInterval(this.id);
    }
  }
  initCarousel() {
    this.carouselBanner = {
      grid: { xs: 1, sm: 1, md: 1, lg: 1, all: 0 },
      slide: 4,
      speed: 500,
      interval: 5000,
      point: {
        visible: true,
        pointStyles: `
          .ngxcarouselPoint {
            list-style-type: none;
            text-align: center;
            padding: 12px;
            margin: 0;
            white-space: nowrap;
            overflow: auto;
            position: absolute;
            width: 100%;
            bottom: 20px;
            left: 0;
            box-sizing: border-box;
          }
          .ngxcarouselPoint li {
            display: inline-block;
            border-radius: 999px;
            background: rgba(255, 255, 255, 0.55);
            padding: 5px;
            margin: 0 3px;
            transition: .4s ease all;
          }
          .ngxcarouselPoint li.active {
              background: white;
              width: 10px;
          }
        `
      },
      load: 2,
      touch: true,
      loop: true,
      easing: 'cubic-bezier(0, 0, 0.2, 1)'
    };
    this.carouselBannerLoad();
  }
  carouselBannerLoad() {
    for (let i = 0; i < this.imgags.length; i++) {
      this.carouselBannerItems.push(
        this.imgags[i]
      );
    }
  }
  loadProfile() {
    this.currentUser = this.userService.getCurrentUser();
  }
  getListItem() {
    this.productService.getAllBeefByPathBySpecies(this.currentUser.area, '2', 'any', 'any').subscribe(res => {
      if (res.message === false) {
        return;
      }
      this.listItem = [];
      res.map(item => {
        if (item.saleoff > 0) {
          item.salePrice = item.price - ((item.price * item.saleoff) / 100);
        }
        if (item.duedate) {
          item.end = new Date(item.duedate);
        }
        item.buy = 1;
        if (item.inventory === 0) {
          item.buy = 0;
        }
        this.listItem.push(item);
      });
      console.log(this.listItem);
    });
  }
  addToCart(item) {
    if (item.inventory === 0) {
      this.toastr.error('', 'Sản phẩm đã bán hết', {
        progressBar: true,
        positionClass: 'toast-bottom-right',
        timeOut: 2000
      });
      return;
    }
    this.productService.get(item.bCode).subscribe(data => {
      if (data[0]) {
        if (data[0].inventory > 0) {
          this.saveItem(item, res => {
            if (res === true) {
              const dialogRef = this.dialog.open(ShoppingCartComponent, {
                width: 'auto',
                height: 'auto',
                data: JSON.parse(localStorage.getItem('orderProduct'))
              });
            }
          });
        } else {
          this.toastr.error('', 'Sản phẩm đã bán hết', {
            progressBar: true,
            positionClass: 'toast-bottom-right',
            timeOut: 2000
          });
        }
      }
    });
  }
  saveItem(item, callback) {
    const orderProduct: any[] = JSON.parse(localStorage.getItem('orderProduct'));
    const index = orderProduct.findIndex(o => o.bCode === item.bCode);
    if (index > -1) {
      this.toastr.error('Thông báo!', 'Sản phẩm đã có trong giỏ hàng', {
        progressBar: true,
        positionClass: 'toast-bottom-right',
        timeOut: 2000
      });
      callback(false);
    } else {
      orderProduct.push(item);
      localStorage.setItem('orderProduct', JSON.stringify(orderProduct));
      this.directionService.sendData(JSON.parse(localStorage.getItem('orderProduct')));
      callback(true);
    }
  }
  computeProgress(itemOff): number {
    return (itemOff.inventory * 100) / itemOff.quantity;
  }
  checkNumber(event, item) {
    console.log(event);
    if (Number(event.target.value) > Number(item.inventory)) {
      item.buy = item.inventory;
    }
  }
  upNumber(item) {
    item.buy = Number(item.buy);
    if (item.buy === item.inventory || item.buy > item.inventory) {
      item.buy = item.inventory;
      return;
    }
    item.buy += 1;
  }
  downNumber(item) {
    item.buy = Number(item.buy);
    if (item.buy === 1 || item.buy === 0) {
      return;
    }
    if (item.buy > item.inventory) {
      item.buy = item.inventory;
      return;
    }
    item.buy -= 1;
  }

}
