import { Component, OnInit, Inject, ViewChild, ElementRef, AfterViewInit } from '@angular/core';
import { MatDialogRef, MatDialog, MAT_DIALOG_DATA } from '@angular/material';
import { Sort } from '@angular/material';
import { OrderService } from '../../services/order.service';
import { UserService } from '../../services/user.service';
@Component({
  selector: 'app-dialog-order-details',
  templateUrl: './dialog-order-details.component.html',
  styleUrls: ['./dialog-order-details.component.scss'],

})
export class DialogOrderDetailsComponent implements OnInit, AfterViewInit {

  currentUser;
  items = [];
  bounes = [];
  pathName = [
    '',
    'Chuck',
    'Ribs',
    'Loin',
    'Round',
    'Brisket',
    'Plate',
    'Flank',
  ];
  weightName = [
    '',
    '250',
    '500',
    '1000',
  ];
  constructor(
    public dialogRef: MatDialogRef<DialogOrderDetailsComponent>,
    private orderService: OrderService,
    private userService: UserService,
    @Inject(MAT_DIALOG_DATA) public data: any) {
    this.loadProfile();
  }
  closeDialog() {
    this.dialogRef.close();
  }
  ngOnInit(): void {

  }
  ngAfterViewInit(): void {
    setTimeout(() => {
      this.getListItem();
    }, 500);
  }
  loadProfile() {
    this.currentUser = this.userService.getCurrentUser();
  }
  getListItem() {
    this.orderService.getOrderDetails(this.data.id_order).subscribe(data => {
      if (data.message === false) {
        return;
      }
      this.items = data;
      this.bounes = data.filter(m => m.type_product === 3);
      console.log(this.items);
      console.log(this.bounes);
    });
  }
  filterProduct(data): Array<any> {
    return data.filter(m => m.type_product !== 3);
  }
}
