
import {map} from 'rxjs/operators';
import { Injectable } from '@angular/core';
// tslint:disable-next-line:import-blacklist
import { Observable ,  BehaviorSubject } from 'rxjs';


import { URLSearchParams } from '@angular/http';
import { ApiService } from './api.service';

@Injectable()
export class OrderService {
    constructor(
        private apiService: ApiService
    ) { }
    getUserOrder(idUser: string): Observable<any> {
        return this.apiService.get('/orders/userorder/' + idUser).pipe(map(result => result));
    }
    postUserOrder(data: any): Observable<any> {
        return this.apiService.post('/orders/userorder', data).pipe(
            map(result => result));
    }
    getOrderDetails(idOrder: string): Observable<any> {
        return this.apiService.get('/orders/' + idOrder).pipe(map(result => result));
    }
}
