import { Component, OnInit, Inject, AfterViewInit } from '@angular/core';
import { MatDialogRef, MatDialog, MAT_DIALOG_DATA, ErrorStateMatcher, MatButton } from '@angular/material';
import { FormGroup, FormControl, FormGroupDirective, NgForm, Validators } from '@angular/forms';
import { CustomerService } from '../../../../services/customer.service';
import { ToastrService } from '../../../../../../node_modules/ngx-toastr';
import { UserService } from '../../../../services/user.service';

@Component({
  selector: 'app-dialog-address-details',
  templateUrl: './dialog-address-details.component.html',
  styleUrls: ['./dialog-address-details.component.scss'],

})
export class DialogAddressDetailsComponent implements OnInit, AfterViewInit {
  currentUser;
  myForm: FormGroup;
  nameFormControl: FormControl = new FormControl('', [
    Validators.required,
  ]);
  matcher = new MyErrorStateMatcher();
  constructor(
    public dialogRef: MatDialogRef<DialogAddressDetailsComponent>,
    private customerService: CustomerService,
    private toastr: ToastrService,
    private userService: UserService,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) {
    this.loadProfile();
  }

  closeDialog() {
    this.dialogRef.close();
  }
  loadProfile() {
    this.currentUser = this.userService.getCurrentUser();
  }
  ngOnInit() {
    this.loadFormControl();
  }
  loadFormControl() {
    this.myForm = new FormGroup({
      'phone': new FormControl(),
      'tagName': this.nameFormControl,
      'address': new FormControl(),
    });
  }
  ngAfterViewInit(): void {
    console.log(this.myForm);
    if (this.data.id_address) {
      this.setDataToInput();
    }
  }
  setDataToInput() {
    setTimeout(() => {
      this.myForm.controls.phone.setValue(this.data.phone);
      this.myForm.controls.tagName.setValue(this.data.tagName);
      this.myForm.controls.address.setValue(this.data.address);
    }, 1);
  }
  onSubmit(myform: NgForm, btnAdd: MatButton) {
    btnAdd.disabled = true;
    if (myform.valid && myform.dirty) {
      if (this.data.id_address) {
        this.data.phone = myform.value.phone;
        this.data.tagName = myform.value.tagName;
        this.data.address = myform.value.address;

        this.customerService.editDeliveryAddress(this.data.id_address, this.data).subscribe(result => {
          this.loadFormControl();
          this.setDataToInput();
          this.toastr.success('', 'Thay đổi thành công!', {
            progressBar: true,
            positionClass: 'toast-bottom-right',
            timeOut: 3000
          });
        });
      } else {
        this.dialogRef.close(myform.value);
      }

    }
  }

}
export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}
