import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
// import { LoginComponent } from './layout/account/login/login.component';
import { CommonModule } from '@angular/common';
import { BrowserModule } from '@angular/platform-browser';
import { ShoppingComponent } from './shopping/shopping.component';
import { UserGuard } from './Guard/user.guard';
import { MainComponent } from './layout/main.component';
import { OrdersComponent } from './orders/orders.component';
import { ProfilesComponent } from './profiles/profiles.component';
import { AddressComponent } from './profiles/address/address.component';
import { ChatComponent } from './chat/chat.component';
import { StoreComponent } from './store/store.component';
import { ApointComponent } from './apoint/apoint.component';
import { ApointDetailsComponent } from './apoint/apoint-details.component';
import { RegistrationComponent } from './account/registration/registration.component';
import { LoginComponent } from './account/login/login.component';
import { ResetPasswordComponent } from './account/resetPassword/resetPassword.component';
import { ShoppingBeefComponent } from './shopping/beef/shopping-beef.component';
import { ShoppingMilkComponent } from './shopping/milk/shopping-milk.component';
import { ShoppingBounesComponent } from './shopping/bounes/shopping-bounes.component';
import { CheckoutComponent } from './shopping/checkout/checkout.component';
import { BeefOriginComponent } from './beef-origin/beef-origin.component';

const routes: Routes = [
  { path: 'signup', component: RegistrationComponent },
  { path: 'signin', component: LoginComponent },
  { path: 'resetPassword', component: ResetPasswordComponent },
  {
    path: 'app', component: MainComponent, canActivate: [UserGuard], children: [
      { path: '', redirectTo: '/app/shopping', pathMatch: 'full' },
      { path: 'shopping', component: ShoppingComponent },

      { path: 'shopping/beef/:id', component: ShoppingBeefComponent },
      { path: 'shopping/milk', component: ShoppingMilkComponent },
      { path: 'shopping/bounes', component: ShoppingBounesComponent },
      { path: 'shopping/checkout', component: CheckoutComponent },
      {
        path: 'apoint', component: ApointComponent, children: [
          { path: ':id', component: ApointDetailsComponent }
        ]
      },
      { path: 'orders', component: OrdersComponent },
      { path: 'beeforigin/:id', component: BeefOriginComponent },
      { path: 'chat', component: ChatComponent },
      { path: 'findstore', component: StoreComponent },
      { path: 'profiles', component: ProfilesComponent },
      { path: 'profiles', component: ProfilesComponent },
      { path: 'profiles/address', component: AddressComponent },
    ]
  },
  { path: '**', redirectTo: '/app/shopping' },

];

@NgModule({
  imports: [
    CommonModule,
    BrowserModule,
    RouterModule.forRoot(routes)
  ],
  exports: [
  ],
})
export class AppRoutingModule { }


