import { Component, OnInit, OnDestroy, Renderer } from '@angular/core';
import { ChatService } from '../services/chat.service';
import { UserService } from '../services/user.service';

@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.scss'],
})
export class ChatComponent implements OnInit, OnDestroy {
  public connection;
  public messages = [];
  public message;
  currentUser;
  constructor(private chatService: ChatService,
    private userService: UserService,
    private renderer: Renderer
  ) {
    this.loadProfile();
  }

  ngOnInit() {
    this.connection = this.chatService.getMessages().subscribe(message => {
      this.messages.push(message);
    });
    // this.connection = this.chatService.getUsers().subscribe(users => {
    //   console.log(users);
    //   this.users = users;
    // });
  }
  ngOnDestroy() {
    // this.connection.unsubscribe();
  }
  loadProfile() {
    this.currentUser = this.userService.getCurrentUser();
  }
  sendMessage() {
    this.chatService.sendMessage(Object.assign({}, JSON.parse(localStorage.getItem('CurrentUser')), {
      message: this.message
    }));
    setTimeout(() => {
      let total = 0;
      const parent = document.getElementsByClassName('message-box')[0];
      const element = document.getElementsByClassName('last-mes')[0];
      for (let index = 0; index < parent.children.length; index++) {
        total += parent.children[index].clientHeight;
      }
      parent.scrollTo({ top: total - element.clientHeight, behavior: 'smooth' });
    }, 500);
    this.message = '';
  }
  computeDate(time): string {
    const milisecondsDiff = new Date().getTime() - new Date(time).getTime();
    const oneSecond = 1000;
    const oneMinute = oneSecond * 60;
    const oneHour = oneMinute * 60;
    const oneDay = oneHour * 24;
    const seconds = Math.floor((milisecondsDiff % oneMinute) / oneSecond);
    const minutes = Math.floor((milisecondsDiff % oneHour) / oneMinute);
    const hours = Math.floor((milisecondsDiff % oneDay) / oneHour);
    const days = Math.floor(milisecondsDiff / oneDay);

    let timeString = '';
    if (days !== 0) {
      timeString += (days !== 1) ? (days + ' ngày trước  ') : (days + ' ngày trước ');
      return timeString;
    }
    if (hours !== 0) {
      timeString += (hours !== 1) ? (hours + ' giờ trước ') : (hours + ' giờ trước ');
      return timeString;
    }
    if (minutes !== 0) {
      timeString += (minutes !== 1) ? (minutes + ' phút trước ') : (minutes + ' phút trước ');
      return timeString;
    }
    if (seconds !== 0 || milisecondsDiff < 1000) {
      timeString += (seconds !== 1) ? (seconds + ' giây trước ') : (seconds + ' giây trước ');
      return timeString;
    }
  }
}
