import { Component, OnInit, Inject, ViewChild, AfterViewInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatTableDataSource, MatPaginator, MatSort } from '../../../../node_modules/@angular/material';
import { ProductService } from '../../services/product.service';
import { DirectionService } from '../../services/direction.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-shopping-cart',
  templateUrl: './shopping-cart.component.html',
  styleUrls: ['./shopping-cart.component.scss']
})
export class ShoppingCartComponent implements OnInit, AfterViewInit {

  totalprice = 0;
  pathName = [
    '',
    'Chuck',
    'Ribs',
    'Loin',
    'Round',
    'Brisket',
    'Plate',
    'Flank',
  ];
  weightName = [
    '',
    '250',
    '500',
    '1000',
  ];
  bounes = [];


  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    public dialogRef: MatDialogRef<ShoppingCartComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private productService: ProductService,
    private router: Router,
    private directionService: DirectionService
  ) {

  }
  ngOnInit() {
  }
  ngAfterViewInit(): void {
    setTimeout(() => {
      this.computeTotalPrice();
    }, 888);
  }
  computeTotalPrice() {
    this.totalprice = 0;
    this.bounes = [];
    this.data.forEach(element => {
      console.log(element);
      if (element.type < 3) {
        this.totalprice += element.priceItemBuy;
      } else {
        this.bounes.push(element);
      }
    });
  }
  delete(item) {
    if (item.type < 3) {
      const index = this.data.map(m => m.bCode).indexOf(item.bCode);
      this.data.splice(index, 1);
      this.productService.post(item.bCode, '1').subscribe(m => {
        if (this.data.length <= 0) {
          this.closeDialog();
        }
        if (this.router.url.includes('checkout')) {
          this.router.navigateByUrl('/app/shopping');
        }
        localStorage.setItem('orderProduct', JSON.stringify(this.data));
        this.directionService.sendData(this.data);
        this.computeTotalPrice();
      });
    } else {
      const index = this.data.map(m => m.id_bounes).indexOf(item.id_bounes);
      this.data.splice(index, 1);
      if (this.data.length <= 0) {
        this.closeDialog();
      }
      localStorage.setItem('orderProduct', JSON.stringify(this.data));
      this.directionService.sendData(this.data);
      this.computeTotalPrice();
    }
  }
  filterProduct(data): Array<any> {
    return data.filter(m => m.type !== 3);
  }
  closeDialog() {
    this.dialogRef.close();
  }
  computeTotal(item): number {
    item.priceItemBuy = (item.buy * item.price) - (((item.buy * item.price) * item.saleoff) / 100);
    return item.priceItemBuy;
  }
}
