import { Component, OnInit } from '@angular/core';
import { UserService } from '../../services/user.service';
import { MatDialog } from '../../../../node_modules/@angular/material';
import { DialogAddressDetailsComponent } from './dialog/dialog-details/dialog-address-details.component';
import { CustomerService } from '../../services/customer.service';
import { ToastrService } from '../../../../node_modules/ngx-toastr';

@Component({
    selector: 'app-address',
    templateUrl: './address.component.html',
    styleUrls: ['./address.component.scss']
})
export class AddressComponent implements OnInit {
    currentUser;
    listUserAddress: Array<any> = [];
    constructor(
        private userService: UserService,
        private customerService: CustomerService,
        private toastr: ToastrService,
        private dialog: MatDialog
    ) {
        this.loadProfile();
    }
    ngOnInit(): void {
        console.log(this.currentUser);
        this.getListUserAddress();
    }
    loadProfile() {
        this.currentUser = this.userService.getCurrentUser();
    }
    getListUserAddress() {
        this.customerService.getListDeliveryAddress(this.currentUser.id_user).subscribe(data => {
            if (data) {
                this.listUserAddress = data;
            }
        });
    }
    changeDefault(address, event) {
        console.log(event);
        if (address.id_address !== this.currentUser.main_address) {
            this.currentUser.main_address = address.id_address;
            // call api
            this.customerService.updateProfile(this.currentUser.id_user, this.currentUser).subscribe(data => {
                if (data.error === false) {
                    // tslint:disable-next-line:max-line-length
                    this.customerService.customerLogin({ email: this.currentUser.email, password: this.currentUser.password }).subscribe(result => {
                        if (result.message === true) {
                            localStorage.setItem('CurrentUser', JSON.stringify(this.currentUser));
                            this.loadProfile();
                            this.toastr.success('', 'Thay đổi thành công!', {
                                progressBar: true,
                                positionClass: 'toast-bottom-right',
                                timeOut: 3000
                            });
                        }
                    });

                }
            });
        } else {
            event.checked = true;
            event.source.checked = true;
        }

    }
    addNew() {
        const dialogRef = this.dialog.open(DialogAddressDetailsComponent, {
            data: { id_address: '' },
            panelClass: '',
        });
        dialogRef.afterClosed().subscribe(result => {
            if (result) {
                this.customerService.addDeliveryAddress(this.currentUser.id_user, result).subscribe(data => {
                    if (data.message === true) {
                        result.id_address = data.insertId;
                        this.listUserAddress.splice(0, 0, result);
                        this.toastr.success('', 'Thêm thành công!', {
                            progressBar: true,
                            positionClass: 'toast-bottom-right',
                            timeOut: 3000
                        });
                    }
                });

            }
        });
    }
    deleteAddress(address, i) {
        this.customerService.deleteDeliveryAddress(address.id_address).subscribe(data => {
            if (data.message === true) {
                this.listUserAddress.splice(i, 1);
                this.toastr.success('', 'Xóa thành công!', {
                    progressBar: true,
                    positionClass: 'toast-bottom-right',
                    timeOut: 3000
                });
            }
        });
    }
    showDetails(address) {
        const dialogRef = this.dialog.open(DialogAddressDetailsComponent, {
            data: address,
        });
    }
}
