
import { map } from 'rxjs/operators';
import { Injectable } from '@angular/core';
// tslint:disable-next-line:import-blacklist
import { Observable } from 'rxjs';


import { ApiService } from './api.service';
import { Customer } from '../models/customer.model';

@Injectable()
export class CustomerService {
    constructor(
        private apiService: ApiService
    ) { }
    customerRegister(data: Customer): Observable<any> {
        return this.apiService.post('/users', data).pipe(map(result => result));
    }
    getUser(idUser: string): Observable<any> {
        return this.apiService.get('/users/' + idUser).pipe(map(result => result));
    }
    checkSignup(data: any): Observable<any> {
        return this.apiService.post('/checksignup', data).pipe(
            map(result => result));
    }
    customerLogin(data: Customer): Observable<any> {
        return this.apiService.post('/login', data).pipe(
            map(result => result));
    }
    customerLoginWithGoogle(data: any): Observable<any> {
        return this.apiService.post('/login/google', data).pipe(
            map(result => result));
    }
    reqResetPassword(data: Customer): Observable<any> {
        return this.apiService.post('/makerequestresetpassword', data).pipe(
            map(result => result));
    }
    changePassword(data): Observable<any> {
        return this.apiService.post('/changepassword', data).pipe(
            map(result => result));
    }
    updateProfile(idUser: string, data: Customer): Observable<any> {
        return this.apiService.put('/users/' + idUser, data).pipe(
            map(result => result));
    }
    addDeliveryAddress(idUser: string, data: any): Observable<any> {
        return this.apiService.post('/useraddress/' + idUser, data).pipe(
            map(result => result));
    }
    editDeliveryAddress(idUserAddress: string, data: any): Observable<any> {
        return this.apiService.put('/useraddress/' + idUserAddress, data).pipe(
            map(result => result));
    }
    deleteDeliveryAddress(idAddress: string): Observable<any> {
        return this.apiService.delete('/useraddress/' + idAddress).pipe(
            map(result => result));
    }
    getListDeliveryAddress(idUserAddress: string): Observable<any> {
        return this.apiService.get('/useraddress/' + idUserAddress).pipe(
            map(result => result));
    }
}
