import { Component, OnInit } from '@angular/core';
import { UserService } from '../services/user.service';
import { MatDialog } from '../../../node_modules/@angular/material';
import { DialogShowDirectionComponent } from './dialog-ShowDirections/dialog-show-direction.component';
import { ApointService } from '../services/apoint.service';
@Component({
  selector: 'app-store',
  templateUrl: './store.component.html',
  styleUrls: ['./store.component.scss'],
})
export class StoreComponent implements OnInit {
  currentUser;
  markers = [];
  zoom = 12;
  lat: number;
  lng: number;
  constructor(
    private userService: UserService,
    private dialog: MatDialog,
    private apointService: ApointService,
  ) {
    this.loadProfile();
  }
  ngOnInit(): void {
    console.log(this.currentUser);
    this.getListApoint();
  }
  loadProfile() {
    this.currentUser = this.userService.getCurrentUser();
    if (this.currentUser.area === 'HCM') {
      this.lat = 10.8509842;
      this.lng = 106.6269628;
    } else if (this.currentUser.area === 'VT') {
      this.lat = 10.4040208;
      this.lng = 107.125572;
    }
  }

  changeArea(event) {
    if (event.target.value === 'HCM') {
      this.lat = 10.8509842;
      this.lng = 106.6269628;
    } else if (event.target.value === 'VT') {
      this.lat = 10.4040208;
      this.lng = 107.125572;
    }
  }

  showDirection(apoint) {

    const dialogRef = this.dialog.open(DialogShowDirectionComponent, {
      data: apoint,
    });
  }
  getListApoint() {
    this.apointService.get(this.currentUser.area).subscribe(data => {
      if (data) {
        this.markers = data;
      }
    });
  }
  converNumber(value): number {
    if (value) {
      return Number(value);
    }
  }




  isOpen(): boolean {
    const end = new Date();
    end.setHours(20);
    const start = new Date();
    start.setHours(8);
    if (new Date().getTime() > end.getTime()) {
      return false;
    } else {
      if (new Date().getTime() < start.getTime()) {
        return false;
      } else {
        return true;
      }
    }
  }

}
