import { Component, OnInit, Inject, AfterViewInit } from '@angular/core';
import { MatDialogRef, MatDialog, MAT_DIALOG_DATA, ErrorStateMatcher, MatButton } from '@angular/material';
import { FormGroup, FormControl, FormGroupDirective, NgForm, Validators } from '@angular/forms';
import { UserService } from '../../services/user.service';
import { CustomerService } from '../../services/customer.service';

@Component({
  selector: 'app-dialog-show-direction',
  templateUrl: './dialog-show-direction.component.html',
  styleUrls: ['./dialog-show-direction.component.scss'],

})
export class DialogShowDirectionComponent implements OnInit, AfterViewInit {

  zoom = 12;
  lat: number;
  lng: number;
  currentUser;
  userDir = '';
  apointDir = { lat: 0, lng: 0 };
  distance: string;
  duration: string;
  listUserAddress: Array<any> = [];
  main_address = new FormControl();
  constructor(
    public dialogRef: MatDialogRef<DialogShowDirectionComponent>,
    private userService: UserService,
    private customerService: CustomerService,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) {
    this.loadProfile();
    this.lat = null;
    this.lng = null;
  }

  closeDialog() {
    this.dialogRef.close();
  }
  ngOnInit() {
    this.getListUserAddress();
  }
  getListUserAddress() {
    this.customerService.getListDeliveryAddress(this.currentUser.id_user).subscribe(data => {
      if (data) {
        this.listUserAddress = data;
        console.log(this.listUserAddress);
      }
    });
  }

  ngAfterViewInit(): void {
    if (this.data) {
      setTimeout(() => {
        this.lat = Number(this.data.latitude);
        this.lng = Number(this.data.longitude);
        this.apointDir.lat = Number(this.data.latitude);
        this.apointDir.lng = Number(this.data.longitude);
      }, 100);

    }
  }
  loadProfile() {
    this.currentUser = this.userService.getCurrentUser();
    this.userDir = this.currentUser.address;
    this.main_address.setValue(this.currentUser.main_address);
  }
  changeSelectAddress(event) {
    this.userDir = event.target.selectedOptions[0].innerText;
  }
  convertString(value): string {
    if (value) {
      return String(value);
    }
  }
  public changeHandler(event: any) {
    console.log(event);
    if (event.routes[0].legs[0].distance) {
      this.distance = event.routes[0].legs[0].distance.text;
    }
    if (event.routes[0].legs[0].duration) {
      this.duration = event.routes[0].legs[0].duration.text;
    }

  }
}
